.cta {
    &.full-width {
        width: 100%;
        margin: 0;
        min-height: 32rem;
        border-radius: 0;
        text-align: center;

        @include breakpoint(XS) {
            min-height: 22rem;
        }
        .cta-inner {
            position: relative;
            max-width: 40rem;
            min-height: 32rem;
            padding-top: 6rem;
            padding-bottom: 6rem;

            @include breakpoint(XS) {
                padding-top: 4rem;
                padding-bottom: 4rem;
            }
        }
    }
}
