//Type scale for a 16px base size with a 1.333 ratio
$type-scale: (
    "sizes": (
        "XS": 0.75rem,
        "S": 0.875rem,
        "M": 1rem,
        "L": 1.3125rem,
        "XL": 1.75rem,
        "2XL": 2.375rem,
        "3XL": 3.1875rem,
    ),
    "line-heights": (
        "XS": 1rem,
        "S": 1.25rem,
        "M": 1.5rem,
        "L": 1.75rem,
        "XL": 2rem,
        "2XL": 2.75rem,
        "3XL": 3.5rem,
    ),
    "scale-ratio": 1.333,
);