.content-block {
    text-align: center;
    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;

    &.text-align-left {
        text-align: left;
    }
    &.text-align-right {
        text-align: right;
    }
    > {
        h1,
        h2,
        h3,
        h4,
        h5,
        h6,
        p,
        ul,
        ol {
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}