html.cssanimations {
    .fade-in {
        opacity: 0;
    }
}

.fade-in {
    animation: fade-in 0.5s ease-out;
    transform-origin: 50% 50%;
    animation-fill-mode: forwards;
}

.bob {
    animation: bob 3s ease-out infinite;
}

[data-aos^=fade][data-aos^=fade] {
    opacity: 0;
    transition-property: opacity,transform;
}