.column-content {
    margin-left: -1rem;
    margin-right: -1rem;

    @include breakpoint(XS) {
        margin-left: 0;
        margin-right: 0;
    }
    &.images-landscape {
        .column-content-img {
            padding-bottom: 80%;
        }
    }
    &.images-portrait {
        .column-content-img {
            padding-bottom: 133.333%;
        }
    }
    .content-lockup {
        margin: 1rem;
        width: calc(33.333% - 2rem - 1px);

        @include breakpoint(XS) {
            width: 100%;
        }
        .column-content-img {
            margin-bottom: 2rem;
            position: relative;
            height: 0;
        }
        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
        }
        p {
            @include size-S;
        }
    }
}