.callouts {
    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;
}

.callout {
    margin-bottom: 1.5rem;
    padding: 2.125rem 2rem;
    width: 100%;

    &:last-child {
        margin-bottom: 0;
    }
    @include breakpoint(XS) {
        padding: 1.5rem 1.125rem;
    }
    header {
        padding-right: 2rem;
    }
    header,
    span {
        position: relative;
        z-index: 3;
    }
    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        @extend %nav-transition;
    }
    &.theme--image {
        &:after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            @extend %nav-transition;
            background: $color-body;
            mix-blend-mode: multiply;
            opacity: 0.4;
        }
    }
    &.theme--standard {
        @include color(background-color, 'background-alt');
    }
    &:hover {
        transform: translateX(0.25rem);
    }
}