.booking-mask {
    border: 1px solid;
    @include color(border-color, 'body');
    @include color(background-color, 'background');

    @include breakpoint(S) {
        width: 100%;
        max-width: 32rem;
    }
    .button.primary {
        margin: 0;
    }
}

.dates-fields {
    border-right: 1px solid;
    @include color(border-color, 'body');
    position: relative;

    @include breakpoint(S) {
        border-right: 0;
        border-bottom: 1px solid;
    }
    .date-field {
        padding: 0.75rem 1rem;
        width: 12rem;
        position: relative;

        input {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
            color: transparent;
            background: transparent;
            border: 0;
            padding: 0;
            margin: 0;
            opacity: 0;
        }
        @include breakpoint(S) {
            width: 50%;
        }
        svg {
            margin-right: 0.75rem;
        }
        &:first-child {
            border-right: 1px solid;
            @include color(border-color, 'body');
        }
    }
    .datepicker-trigger {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
    }
}

.rooms-guests-fields {
    position: relative;

    .rooms-guests {
        padding: 0.75rem 1rem;
        min-width: 14rem;
        cursor: pointer;

        svg {
            margin-right: 0.75rem;
        }
    }
}

.banner-block {
    .booking-mask {
        margin-top: 4.5rem;
    }
}

.rooms-guests-select {
    // some css for this is in _base-styles.scss to stop it flashing up on page load
    @extend %transition;
    z-index: 5;

    @include breakpoint(XS) {
        left: auto;
        right: 0;
    }
    .select-inner {
        box-shadow: 0px 4px 8px rgba($color-body, 0.15);
        padding: 1.5rem;
        width: 100%;
        border-radius: 2px;
        box-shadow: 4px 8px 24px rgba(0, 10, 44, 0.075);
        @include color(background-color, 'background');

        &.no-padding {
            padding: 0;
        }
        footer {
            padding-top: 1.5rem;
            border-top: 1px solid;
            @include color(border-color, 'body-10');

            a {
                @include color(color, 'body');

                &:hover {
                    @include color(color, 'accent-primary');
                }
            }
        }
        .selector-value {
            width: 3rem;
        }
    }
    &:before {
        content: "";
        position: absolute;
        width: 0;
        height: 0;
        border-style: solid;
        border-width: 0 7px 7px 7px;
        border-color: transparent transparent #ffffff transparent;
        top: -6px;
        left: 1rem;

        @include breakpoint(XS) {
            left: auto;
            right: 1rem;
        }
    }
    &.active {
        top: calc(100% + 1.5rem);
        visibility: visible;
        opacity: 1;
    }
}