//Create utility classes here
//====================================================

.size-xs {
    @include size-XS;
}

.size-s {
    @include size-S;
}

.size-m {
    @include size-M;
}

.size-l {
    @include size-L;
}

.size-xl {
    @include size-XL;
}

.size-2xl {
    @include size-2XL;
}

.size-3xl {
    @include size-3XL;
}

@include breakpoint(M) {

    .md\:size-m {
        @include size-M;
    }
    
    .md\:size-s {
        @include size-S;
    }
    
    .md\:size-xs {
        @include size-XS;
    }

}

@include breakpoint(XS) {

    .xs\:size-xs {
        @include size-XS;
    }
    
    .xs\:size-s {
        @include size-S;
    }
    
    .xs\:size-m {
        @include size-M;
    }
    
    .xs\:size-l {
        @include size-L;
    }
    
    .xs\:size-xl {
        @include size-XL;
    }
    
    .xs\:size-2xl {
        @include size-2XL;
    }
    
    .xs\:size-3xl {
        @include size-3XL;
    }
}